import React, { useState, useEffect } from 'react'

import './App.css'

const image = require('./chat.png')


const axios = require('axios')
const App = () => {

  const [mensagems, setMensagems] = useState([])
  const [update, setUpdate] = useState(false)
  const [updateTest, setUpdateTest] = useState(false)

  const Atualizar = async () => {
    const response = await axios.get('http://167.172.137.69:8000/messages');
    setMensagems(response.data)
    console.log(response.data)
  }

  useEffect(() => {
    if (!updateTest) {
      console.log("ok")
      setUpdateTest(true);
    }
    if (!update) {
      Atualizar()
      setUpdate(true)
      setTimeout(() => {
        setUpdate(false)
      }, 1000)
    }
  }, [update])

  return (
    <>
        {mensagems.map((msg) => {
        return (
          <div class="category-info">
            <div className="image">
        <h1 className="nome">{msg.username}</h1>
              <p className="mensagem">{msg.mensagem}</p>
              <img src={image} alt="myimage" />
            </div>
          </div>
        )
      })}
    </>
  )
}

export default App;